@import url(https://fonts.googleapis.com/css?family=Roboto:400,700,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  line-height: 1;
  background-color: #f7f7f7;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ol,
ul {
  list-style: none;
}

.app {
  text-align: center;
  max-width: 500px;
  margin: auto;
}

header {
  background-color: white;
  overflow: hidden;
  display: flex;
}

header {
  background-color: white;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 5px;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1);
}

header button {
  background-color: transparent;
  border: 0;
  width: 50px;
  cursor: pointer;
}

header button img {
  width: 100%;
}

header .fl {
  float: left;
  width: 33%;
}

header .fl:first-child {
  text-align: left;
}

header .fl:last-child {
  text-align: right;
}



.person .person-description {
  text-align: center;
  padding: 1px 20px;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1);
  border-radius: 1px;
  width: 291px;
  background-color: white;
}

.person .person-name-age {
  font-weight: bold;
  font-size: 19px;
  margin-bottom: 10px;
  margin-top: 0;
  font-family: -webkit-pictograph;
}

.person .person-info {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 20px;
}

.person img {
  width: 292px;
  height: 450px;
  border-radius: 18px 18px 0px 0px;
  margin-top: 30px;
  object-fit: cover;
}

#actions {
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  margin: 0 auto;

}

#actions button {
  width: 50px;
  height: 50px;
  margin-right: 85px;
  padding: 12px;
  border: 0;
  border-radius: 50px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  float: left;
  cursor: pointer;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.1);
}

#actions button img {
  width: 100%;
  height: auto;
}

#actions button:nth-of-type(2),
#actions button:nth-of-type(3) {
  width: 60px;
  height: 60px;
  padding: 15px;
  margin-left: 108px;
}

#lonely {
  padding: 10px;
  border-radius: 1px;
  background-color: white;
  border-top: 1px solid #eee;
}

#liked-people {
  margin: auto;
  overflow: hidden;
}

#liked-people .liked-person {
  display: inline-block;
  width: 33%;
  margin-top: 10px;
}

#liked-people .liked-person-image {
  border-radius: 5px;
}

#liked-people .liked-person-image img {
  width: 140px;
  height: 100px;
  object-fit: cover;
  object-position: 50% 50%;
}

.matches {
  margin-left: 180px;
  margin-top: 40px;
}

/* Stolen CSS */
.pulse {
  margin: auto;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: #FFDEDF;
  box-shadow: 0 0 0 rgba(204, 169, 44, 0.4);
  -webkit-animation: pulse 2s infinite;
  animation: pulse 2s infinite;
  margin-top: 50px;
  margin-bottom: 35px;
  border: 3px solid #F5C9CA;
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pulse img {
  width: 84%;
  height: 86%;
  border-radius: 100px;
  border: 5px solid white;
}

.pulse:hover {
  -webkit-animation: none;
          animation: none;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(241, 201, 202, 0.4);
  }

  100% {
    -webkit-box-shadow: 0 0 0 35px rgba(241, 201, 202, 0);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(241, 201, 202, 0.4);
  }

  100% {
    box-shadow: 0 0 800px 35px rgba(119, 5, 7, 0);
  }
}
.sideContainer {
    background-color: white;
    height: 98vh;
    border-right: 1px solid #e8e8e9;
}
.progress {
  transform: rotate(90deg);
  margin-top: 266px;
  width: 400px;
  margin-left: -59px;
}

.progress-bar.progress-bar-animated.progress-bar-striped {
  background-color: #dc3545;
}

.progressbarstyle {
  background-color: white;
  height: 100vh;
  width: 20%;
  box-shadow: 10px 10px 15px -10px;
}

.form-control {
    display: block;
    width: 100%;
    height: calc(1.5em + .75rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: rgb(218, 75, 99);
    background-color: #fff;
    border: 1px solid black;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}
.MuiInputBase-input {

    width: 50%
}
.chatContainer {
    margin-top: 20px;
    width: 100%;
    padding-top: 20px;
}

form {
    width: 100%;
    bottom: 0;
}

.chatHeader {
    position: fixed;
    color: black;
    text-align: center;
    width: 65%;
    top: 0;
    font-size: 10px;

}

h4 {
    font-size: 23px;
}
.MidleContainer {
    box-shadow: 9px -4px 4px -11px;
    height: 95vh;
    padding-top: 45px;
    padding-right: 37px;
    width: 280px;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
}
